import { GET_LYRICS } from './lyrics-types';
import Axios from 'axios';

export const getLyrics = (data) => async (dispatch) => {
    try {
      const { data: { lyrics } } = await Axios.get(`https://api.lyrics.ovh/v1/${data.artist}/${data.song}`);
      // const { data: { links: { download } } }  = await Axios.get('https://api.unsplash.com/photos/random/?client_id=ajn-jA_ng9E3bbmfKNAN99Oo9zT-ZGyjwY4QdJsmTfk');
      const { data: { results } }  = await Axios.get(`https://api.unsplash.com/search/photos/?query=${data.artist}&client_id=ajn-jA_ng9E3bbmfKNAN99Oo9zT-ZGyjwY4QdJsmTfk`);
      
      return dispatch({
        type: GET_LYRICS,
        payload: lyrics,
        url: results[0]?.links.download
      });
        
    } catch (error) {
        console.log(error);
    }
};