import { GET_LYRICS } from '../actions/lyrics/lyrics-types';

const initialState = {
    lyrics: '',
    img: ''
};

export default function ( state = initialState, action ) {
    switch(action.type) {
        case GET_LYRICS:
            return {
                ...state,
                lyrics: action.payload,
                img: action.url
            };
        default: return {...state};
    }
}