import React from "react";
import { useForm } from "react-hook-form";
import { getLyrics } from '../store/actions/lyrics/lyrics-actions';
import { connect } from "react-redux";

function SearchForm({ getLyrics }) {
  const { register, handleSubmit, errors } = useForm();
  const onSubmit = data => {
    // console.log(data);
    getLyrics(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="search">
        <div>
            <label htmlFor="artist">Artist</label>
            <input name="artist" defaultValue="" ref={register({
                required: {
                    value: true,
                    message: 'This field is required.'
                }
            })} />
            <p> {errors.artist?.message} </p>
        </div>
        <div>
            <label htmlFor="song">Song name</label>
            <input name="song" defaultValue="" ref={register({
                required: {
                    value: true,
                    message: 'This field is required.'
                }
            })} />
            <p> {errors.song?.message} </p>
        </div>

      <input type="submit" value="SEARCH"/>
    </form>
  );
};

export default connect(null, { getLyrics })(SearchForm);